<head>
<meta http-equiv="Content-Security-Policy" content="upgrade-insecure-requests">
</head>

<template>
  <q-page class="ub-page">
    <div class="row items-center" style="margin-bottom: 10px;">
      <div class="col-md-12 q-ma-sm">
        <div class="text-subtitle1">
          <!-- Check out <a href="https://github.com/slanatech/swagger-stats" target="_blank">swagger-stats</a> project to see this dashboard in action -->
        </div>
      </div>
    </div>
    <db-dashboard v-if="ready" :dbspec="dbspec" :dbdata="dbdata" :dark="dark"> </db-dashboard>
  </q-page>
</template>

<script>
import { DbData } from 'dashblocks';
import pathOr from 'ramda/src/pathOr';
import { mapState } from 'vuex';
import utils from '../utils.js';

// File '../data/errors.json' contains example dashboard data
import dashboardData from '../data/mikekuan2.json';
import { demodashboard } from '../mixins/demodashboard';

export default {
  name: 'test',
  mixins: [demodashboard],
  data() {
    return {
      timer: null,
      isDark: false,
      apiOpMethod: null,
      apiOpPath: null,
      dbdata: new DbData(),
      dbspec: {
        layout: {
          type: 'grid',
          size: 12
        },
        colorScheme: 'default',
        // prettier-ignore
        widgets: [
          { id: 'wR1', type: 'DbNumber', cspan: 3, properties: { title: '合計有效門號數', subtitle: '', icon: 'fa fa-user' } },
          { id: 'wR2', type: 'DbNumber', cspan: 2, properties: { title: '本月新增門號數', subtitle: '', icon: 'fa fa-plus' } },
          { id: 'wR3', type: 'DbNumber', cspan: 2, properties: { title: '本月停機門號數', subtitle: '', icon: 'fa fa-minus' } },
          { id: 'wR4', type: 'DbNumber', cspan: 2, properties: { title: '合計晶圓廠數量', subtitle: '', icon: 'fa fa-microchip' } },
          { id: 'wR5', type: 'DbNumber', cspan: 3, properties: { title: '合計封測廠數量', subtitle: '', icon: 'fa fa-industry' } },
          {
            id: 'w20',
            type: 'DbChartjsDoughnut',
            cspan: 6,
            height: 200,
            properties: {
              options: {
                title: {display: true, text: '門號狀態幒覽', position: 'top'},
                legend: { position: 'right' }
              }
            }
          },
          {
            id: 'w1',
            type: 'DbDygraphsBar',
            cspan: 6,
            height: 200,
            properties: {
              options: {
                stackedGraph: true,
                ylabel: '各廠門號統計',
                labels: ['Date', 'Active', 'Suspend', 'Cancel'],
                legend: 'always'
              }
            }
          },
          {
            id: 'w2',
            type: 'DbDygraphsBar',
            cspan: 6,
            height: 200,
            properties: {
              options: {
                stackedGraph: true,
                ylabel: '當月流量統計(上月)',
                labels: ['Date', '流量'],
                legend: 'follow'
              }
            }
          },
          {
            id: 'w3',
            type: 'DbChartjsLine',
            cspan: 6,
            height: 200,
            properties: {
              options: {
                title: {display: true, text: '各廠流量趨勢(上月)', position: 'top'},
                legend: { position: 'right' }
              }
            }
          },
        ]
      },
      ready: false
    };
  },
  computed: {
    ...mapState({
      dark: state => state.layout.dark
    })
  },
  watch: {},
  mounted() {
    this.initialize();
    this.ready = true;
  },
  methods: {
    initialize: function() {
      // Init dashboard color scheme from state
            let dataTwoSeries = {
        labels: ['January', 'February', 'March', 'April'],
        datasets: [
          {
            label: 'Data One',
            data: [10, 20, 30, 100]
          },
          {
            label: 'Data Two',
            data: [50, 10, 70, 11]
          }
        ]
      };
      
      this.dbspec.colorScheme = this.dashboardColorScheme;

      this.dbdata.setWData('w1', { data: [] });
      this.dbdata.setWData('w2', { data: [] });

      this.dbdata.setWData('wR1', { value: 0, trend: [] });
      this.dbdata.setWData('wR2', { value: 0, trend: [] });
      this.dbdata.setWData('wR3', { value: 0, trend: [] });
      this.dbdata.setWData('wR4', { value: 0, trend: [] });
      this.dbdata.setWData('wR5', { value: 0, trend: [] });

      this.dbdata.setWData('w3', { data: { labels: [], datasets: [{ data: [[],[]] }] } });
      
      this.dbdata.setWData('w20', { data: { labels: [], datasets: [{ data: [] }] } });
      this.updateStats();
    },

    updateStats: function() {
      let dthData = [];
      let dthData2 = [];
      let trendsData = [[], [], [], [], []];
      let statsContainer = dashboardData;
      let timelineSorted = utils.getSortedTimeline(statsContainer);
      for (let entry of timelineSorted) {
          dthData.push([
          new Date(entry.ts),
          pathOr(0, ['stats', 'active'], entry),
          pathOr(0, ['stats', 'suspend'], entry),
          pathOr(0, ['stats', 'cancel'], entry)
        ]);
      }
      this.dbdata.setWData('w1', { data: dthData });
      for (let entry of timelineSorted) {
          dthData2.push([
          new Date(entry.ts),
          pathOr(0, ['stats', 'datasize'], entry)
        ]);
      }
      this.dbdata.setWData('w2', { data: dthData2 });
      let requestsTotal1 = pathOr(0, ['all', '1'], statsContainer);
      let requestsTotal2 = pathOr(0, ['all', '2'], statsContainer);
      let requestsTotal3 = pathOr(0, ['all', '3'], statsContainer);
      let requestsTotal4 = pathOr(0, ['all', '4'], statsContainer);
      let requestsTotal5 = pathOr(0, ['all', '5'], statsContainer);
      let reqTrendMax = Math.max(...trendsData[0]);
      this.dbdata.setWData('wR1', { value: requestsTotal1, trend: trendsData[0] });
      this.dbdata.setWData('wR2', { value: requestsTotal2, trend: trendsData[0] });
      this.dbdata.setWData('wR3', { value: requestsTotal3, trend: trendsData[0] });
      this.dbdata.setWData('wR4', { value: requestsTotal4, trend: trendsData[0] });
      this.dbdata.setWData('wR5', { value: requestsTotal5, trend: trendsData[0] });

      let statusData = pathOr(null, ['status'], statsContainer);
      if (!statusData) {
        return;
      }
      
      let codesData = utils.getKVObjSortedArray(pathOr({}, ['statuscode'], statusData), false);
      this.dbdata['w20'].data.labels = codesData.map(x => x.k);
      this.dbdata['w20'].data.datasets[0].data = codesData.map(x => x.v);
      this.dbdata.touch('w20');

      let statusData2 = pathOr(null, ['data'], statsContainer);
      if (!statusData2) {
        return;
      }

      let codesData2 = utils.getKVObjSortedArray(pathOr({}, ['gb'], statusData2), false);
      this.dbdata['w3'].data.labels = codesData2.map(x => x.k);
      this.dbdata['w3'].data.datasets[0].data = codesData2.map(x => x.v);
      this.dbdata.touch('w3');
      
    }
  }
};
</script>
