<template>
  <q-page class="ub-page">
    <div class="row items-center" style="margin-bottom: 10px;">
      <div class="col-md-12 q-ma-sm">
        <div class="text-h3">3月展覽頁面</div>
        <div class="text-subtitle1">
          <!-- Check out <a href="https://github.com/slanatech/swagger-stats" target="_blank">swagger-stats</a> project to see this dashboard in action -->
        </div>
      </div>
    </div>
    <db-dashboard v-if="ready" :dbspec="dbspec" :dbdata="dbdata" :dark="dark" style="width: 1400px;"> </db-dashboard>
  </q-page>
</template>
<script src="https://cdn.jsdelivr.net/npm/vue"></script>
<script src="https://unpkg.com/vue-currency-filter"></script>

<script>
import { DbData } from 'dashblocks';
import pathOr from 'ramda/src/pathOr';
import { mapState } from 'vuex';
import utils from '../utils.js';
import Vue from 'vue';

// File '../data/summary.json' contains example dashboard data
import dashboardData from '../data/mikekuan.json';
//import dashboardData2 from '../data/apioperation.json';
import { demodashboard } from '../mixins/demodashboard';

export default {
  name: 'SummaryView',
  mixins: [demodashboard],
  data() {
    return {
      timer: null,
      dbdata: new DbData(),
      dbspec: {
        layout: {
          type: 'grid',
          size: 12
        },
        colorScheme: 'default',
        // prettier-ignore
        widgets: [
          { id: 'hgb0', type: 'DbNumber', cspan: 2, properties: { title: '合計有效門號數'} },
          { id: 'hgb0', type: 'DbNumber', cspan: 2, properties: { title: '本月新增門號數'} },
          { id: 'hgb0', type: 'DbNumber', cspan: 2, properties: { title: '本月停機門號數'} },
          { id: 'hgb0', type: 'DbNumber', cspan: 2, properties: { title: '合計晶圓廠數量'} },
          { id: 'hgb0', type: 'DbNumber', cspan: 2, properties: { title: '合計封測廠數量'} },
          {
            id: 'w16',
            type: 'DbEasyPie',
            cspan: 5,
            height: 200,
            properties: {
              options: {
                stackedGraph: true,
                title: '月出帳趨勢',
                labels: ['Date', 'max_time', 'avg_time', 'Client Error','Server Error'],
                legend: 'follow',
              },
              opacity: 0.5
            }
          },
          {
            id: 'w16',
            type: 'DbDygraphsDateTimeHistogram',
            cspan: 5,
            height: 200,
            properties: {
              options: {
                stackedGraph: true,
                title: '月出帳趨勢',
                labels: ['Date', 'max_time', 'avg_time', 'Client Error','Server Error'],
                legend: 'follow',
              },
              opacity: 0.5
            }
          },
          {
            id: 'w16',
            type: 'DbDygraphsDateTimeHistogram',
            cspan: 5,
            height: 200,
            properties: {
              options: {
                stackedGraph: true,
                title: '月出帳趨勢',
                labels: ['Date', 'max_time', 'avg_time', 'Client Error','Server Error'],
                legend: 'follow',
              },
              opacity: 0.5
            }
          },
          {
            id: 'w16',
            type: 'DbDygraphsDateTimeHistogram',
            cspan: 5,
            height: 200,
            properties: {
              options: {
                stackedGraph: true,
                title: '月出帳趨勢',
                labels: ['Date', 'max_time', 'avg_time', 'Client Error','Server Error'],
                legend: 'follow',
              },
              opacity: 0.5
            }
          }
        ]
      },
      ready: false
    };
  },
  mounted() {
    this.initialize();
    this.ready = true;
  },

  methods: {
    initialize: function() {
      this.dbspec.colorScheme = this.dashboardColorScheme;
      this.updateStats();
    },

    updateStats: function() {
      // Update numbers
      let statsContainer = dashboardData;
      //let statsContainer2 = dashboardData2;
      let totalHGB = pathOr(0, ['HGB', 'ACCT_COUNT'], statsContainer);
      let billdateHGBN = pathOr(0, ['HGBN', 'BILL_DATE'], statsContainer);
      let totalHGBN = pathOr(0, ['HGBN', 'ACCT_COUNT'], statsContainer);

      let trendsData = [[], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], []];
      let dthData = [];
      let cpuData = [];
      let memData = [];
      let lagData = [];
      let asData = [];

      let timelineSorted = utils.getSortedTimeline(statsContainer);
      //let timelineSorted2 = utils.getSortedTimeline(statsContainer2);
      //let trendReqData =  [];
      for (let entry of timelineSorted) {
        dthData.push([
          new Date(entry.ts),
          pathOr(0, ['stats', 'max_time'], entry),
          pathOr(0, ['stats', 'avg_time'], entry),
          pathOr(0, ['stats', 'client_error'], entry),
          pathOr(0, ['stats', 'server_error'], entry)
        ]);
        asData.push([new Date(entry.ts), pathOr(0, ['stats', 'apdex_score'], entry)]);
        cpuData.push([new Date(entry.ts), pathOr(0, ['sys', 'cpu'], entry)]);
        memData.push([new Date(entry.ts), pathOr(0, ['sys', 'heapTotal'], entry) / 1048576, pathOr(0, ['sys', 'heapUsed'], entry) / 1048576]);
        lagData.push([new Date(entry.ts), pathOr(0, ['sys', 'lag'], entry)]);
        // Trends
        trendsData[0].push(pathOr(0, ['stats', 'requests'], entry));
        trendsData[1].push(pathOr(0, ['stats', 'apdex_score'], entry));
        trendsData[2].push(pathOr(0, ['stats', 'req_rate'], entry));
        trendsData[3].push(pathOr(0, ['stats', 'err_rate'], entry));
        trendsData[4].push(pathOr(0, ['sys', 'cpu'], entry));
        trendsData[5].push(pathOr(0, ['sys', 'heapUsed'], entry));
        trendsData[6].push(pathOr(0, ['stats', 'requests2'], entry));
        trendsData[7].push(pathOr(0, ['stats', 'apdex_score'], entry));
        trendsData[8].push(pathOr(0, ['stats', 'req_rate'], entry));
        trendsData[9].push(pathOr(0, ['stats', 'err_rate'], entry));
        trendsData[10].push(pathOr(0, ['sys', 'cpu'], entry));
        trendsData[11].push(pathOr(0, ['sys', 'heapUsed'], entry));
        trendsData[12].push(pathOr(0, ['stats', 'errors'], entry));
        trendsData[13].push(pathOr(0, ['stats', 'success'], entry));
        trendsData[14].push(pathOr(0, ['stats', 'redirect'], entry));
        trendsData[15].push(pathOr(0, ['stats', 'client_error'], entry));
        trendsData[16].push(pathOr(0, ['stats', 'server_error'], entry));
        trendsData[17].push(pathOr(0, ['sys', 'lag'], entry));
      }

      let lastTimeBucket = utils.getCurrentTimelineBucket(statsContainer);
      /* let requestRate = pathOr(0, ['stats', 'req_rate'], lastTimeBucket); */
      let errRate = pathOr(0, ['stats', 'err_rate'], lastTimeBucket);

      let reqTrendMax = Math.max(...trendsData[0]);
      let reqRateTrendMax = Math.max(...trendsData[2]);
      this.dbdata.setWData('hgb0', { value: pathOr(0, ['HGB', 'BILL_DATE'], statsContainer) });
      this.dbdata.setWData('hgb1', { value: totalHGB, trend: trendsData[0] });
      this.dbdata.setWData('hgb2', { value: pathOr(0, ['HGB', 'DEVICE_SUB_COUNT'], statsContainer), trend: trendsData[1] });
      this.dbdata.setWData('hgb3', { value: pathOr(0, ['HGB', 'LAST_AMOUNT'], statsContainer), trend: trendsData[2] });
      this.dbdata.setWData('hgb4', { value: errRate, trend: trendsData[3], trendMax: reqRateTrendMax });
      this.dbdata.setWData('hgb5', { value: pathOr(0, ['sys', 'cpu'], statsContainer), trend: trendsData[4] });
      let { value, qualifier } = utils.formatBytes(pathOr(0, ['sys', 'heapUsed'], statsContainer), 2);
      this.dbdata.setWData('hgb6', { value: value, qualifier: qualifier, trend: trendsData[5] });

      this.dbdata.setWData('hgbn0', { value: pathOr(0, ['HGBN', 'BILL_DATE'], statsContainer) });
      this.dbdata.setWData('hgbn1', { value: totalHGBN, trend: trendsData[6] });
      this.dbdata.setWData('hgbn2', { value: pathOr(0, ['HGBN', 'DEVICE_SUB_COUNT'], statsContainer), trend: trendsData[7] });
      this.dbdata.setWData('hgbn3', { value: pathOr(0, ['HGBN', 'LAST_AMOUNT'], statsContainer), trend: trendsData[8] });
      this.dbdata.setWData('hgbn4', { value: errRate, trend: trendsData[9], trendMax: reqRateTrendMax });
      this.dbdata.setWData('hgbn5', { value: pathOr(0, ['sys', 'cpu'], statsContainer), trend: trendsData[10] });
      this.dbdata.setWData('hgbn6', { value: value, qualifier: qualifier, trend: trendsData[11] });

      this.dbdata.setWData('w7', { value: pathOr(0, ['all', 'errors'], statsContainer), trend: trendsData[6], trendMax: reqTrendMax, total: totalHGB });
      this.dbdata.setWData('w8', { value: pathOr(0, ['all', 'success'], statsContainer), trend: trendsData[7], trendMax: reqTrendMax, total: totalHGB });
      this.dbdata.setWData('w9', { value: pathOr(0, ['all', 'redirect'], statsContainer), trend: trendsData[8], trendMax: reqTrendMax, total: totalHGB });
      this.dbdata.setWData('w10', {
        value: pathOr(0, ['all', 'client_error'], statsContainer),
        trend: trendsData[9],
        trendMax: reqTrendMax,
        total: totalHGB
      });
      this.dbdata.setWData('w11', {
        value: pathOr(0, ['all', 'server_error'], statsContainer),
        trend: trendsData[10],
        trendMax: reqTrendMax,
        total: totalHGB
      });
      this.dbdata.setWData('w12', { value: pathOr(0, ['sys', 'lag'], statsContainer), trend: trendsData[11] });

      this.dbdata.setWData('w14', { data: cpuData });
      this.dbdata.setWData('w15', { data: memData });
      this.dbdata.setWData('w16', { data: dthData });
      this.dbdata.setWData('w17', { data: lagData });
      this.dbdata.setWData('w18', { data: asData });
    }
  }
};
</script>
